//seclect2
.select2-container--default .select2-selection--single {
  border: none;
  border-bottom:1px solid rgba(0,0,0,0.12);
  border-radius: 0;
  font-size: $default-font-size;
}

.select2-container--focus .select2-selection--single{
  border-bottom: 2px solid $pink;
}

.select2-container--default .select2-selection--single .select2-selection__rendered {
  color: rgba(0,0,0,0.26);
}

.select2-container--above.select2-container--default .select2-selection--single .select2-selection__rendered,
.select2-container--below.select2-container--default .select2-selection--single .select2-selection__rendered {
  color:#000 !important;
}

.select2-container--default .select2-selection--single .select2-selection__arrow b {
  border-color: rgba(0,0,0,0.26) transparent transparent transparent;
  border-width: 7px 6px 0 6px;
}
.select2-container--default.select2-container--open .select2-selection--single .select2-selection__arrow b {
  border-color:  transparent transparent rgba(0,0,0,0.26) transparent;
  border-width: 0 6px 7px 6px;
}

.select2-container *{
  border-radius: 0;
  outline: none;
}

.select2-results__option {
  padding: 16px;
}

.select2-container--default .select2-results__option--highlighted[aria-selected] {
  background: none !important;
  color: #000;
}

.select2-container--default .select2-results__option[aria-selected=true] {
  background: none !important;
  color: $pink;
}

.select2-dropdown {
  z-index: 10 !important;
  border: none;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 3px;
}

