.container {
  background-size: cover;
}

.header {
  @include breakpoint($tablet) {
    overflow: hidden;
  }
}

// body:not(.innerPage) .header,
.videoBgWrapper,
.backgroundGraphic {
  height: 100vh;

  *::-webkit-media-controls-panel {
    display: none !important;
    -webkit-appearance: none;
  }

  *::--webkit-media-controls-play-button {
    display: none !important;
    -webkit-appearance: none;
  }

  *::-webkit-media-controls-start-playback-button {
    display: none !important;
    -webkit-appearance: none;
  }
}

.videoBgWrapper {
  position: relative;
}

.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 304px;
  z-index: 100;
  background: #fff;
  padding: 2rem 4rem;
  font-size: $smaller-font-size-fixed;
  @include transition(all 0.3s ease-out 0s);
  line-height: 40px;
  @include transform(translate3d(-100%,0,0));
  z-index: 101;


  &__overlay {
    @extend .popupOverlay;
  }

  .sidebar--show & {
    @include transform(translate3d(0,0,0));

    &__overlay {
      visibility: visible;
      opacity: .48;
      background: #000;
    }
  }

  .icon-close {
    position: absolute;
    right: 2rem;
    top: 2rem;
    margin: 0;
    color: #000;
    opacity: .45;
    font-size: 28px;
    cursor: pointer;
  }

  a:not(.btn) {
    color: $dark-text;
  }

  &__menu {
    clear: both;
    font-size: $smaller-font-size-fixed;
    text-transform: uppercase;

    li {
      position: relative;

      a {
        text-decoration: none;
      }
    }

  }
  .topSection__login {
    width: 100%;
    font-weight: bold;
    margin-top: 4rem;
  }
}

.topSection{
  position: fixed;
  left: 0;
  right: 0;
  z-index: 20;
  font-size: $smaller-font-size;
  top: 0;
  padding: 2rem 7rem 2rem 7rem;;
  line-height: 4rem;
  background-color: transparent;
  text-align: right;
  max-height: 40px;
  box-sizing: content-box;
  font-size: $smaller-font-size-fixed;
  @include transition(background 0.3s ease-out 0s);

  .icon-navicon {
    font-size: 28px;
    color: #fff;
  }

  .mainMenu {
    display: inline-block;
    margin: 0;
    padding: 0;

    li {
      display: inline-block;
      margin-left: 2rem;
      text-transform: uppercase;
      position: relative;
      z-index: 200;

      &.forShow {
        display: none;
      }

      > a {
        color: #fff;
        text-decoration: none;
      }
    }
  }

  &:before {
    background: #fff;
    content: '';
    display: block;
    @extend .absoluteCenter;
    z-index: 1;
    opacity: 0;
    @include transition(all 0.25s ease-out );
  }

  &__logo{
    float: left;
    position: relative;
    z-index: 10;
    width: 189px;
    height: 40px;
    background: url(/img/lobster_Logo.svg);
    background-repeat: no-repeat;
    background-size: contain;

    @include breakpoint($tablet)  {
      width: 150px;
      height: 33px;
      // margin-top: 5px;
    }

    .sticky-top &,
    .innerPage &,
    &.small {
      background: url(/img/Lobster_Logo_dark.svg);
    }

    &.small {
      width: 118px;
      height: 25px;
      margin-bottom: 6rem;
    }
  }

  @include breakpoint($tablet)  {
    padding: 2.2rem 3rem 2.2rem 4.5rem;
  }

  @include breakpoint($mobile)  {
    display: flex;
    justify-content: space-between;
    padding: 2.2rem 3rem 2.2rem 4.5rem;
    padding: 2rem 5rem;
    font-size: $smaller-font-size-fixed;
    line-height: 40px;

    .topSection__logo {
      background: url(/img/lobster_symbol.svg);
      background-repeat: no-repeat;
      width: 24px;
      height: 35px;
    }

    .forShow,.mHide {
      display: none !important;
    }

    .burgerMenu {
      display: block;
      width: 30%;
      text-align: left;
    }

    .mainMenu {
      width: 30%;
      a {
        font-size: $smaller-font-size-fixed;
      }
    }
  }

  .sticky-top &,
  .innerPage & {
    box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.2);
    background: #fff;

    .icon-navicon {
      color: #6f6f6f;
    }

    li.forShow {
      display: inline-block;
      opacity: 1;
    }

    a:not(.logo--small) {
      position: relative;
      z-index: 10;
    }

    a:not(.btn) {
      color: $top-menu-color;
    }

    &:before {
      opacity: 0.9;
    }
  }
}


.footer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background-color: #3D3D3D;
  margin: 2rem;
  padding: 7rem 5rem 2rem 5rem;
  color: #fff;
  @include font-size($smaller-font-size);

  @include breakpoint($mobile) {
    font-size: 1.6rem;
  }

  &__line {
    display: none;
    background: #fff;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.13);
    padding: 2rem 0;
    text-align: center;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 11;

    @include breakpoint($mobile) {
       .sticky-top &{
        display: block;
      }
    }

    .oh & {
      display: none;
    }

    .btn {
      @extend .static-sm;
    }
  }

  &__slogan {
    width: 50%;

    p {
      margin: 0;
    }
    a {
      color: $pink;
      font-weight: bold;
    }
  }

  &__sloganText {
    overflow: hidden;
  }

  &__logo {
    display: inline-block;
    float: left;
    background-image: url(/img/lobster_symbol.svg);
    width: 24px !important;
    height: 35px;
    margin-right: 4px;

    @include breakpoint($mobile) {
      margin-right: 0;
    }

  }

  &__menu {
    width: 50%;
    text-align: right;
    font-size: $default-font-size;

    @include breakpoint($mobile) {
      &__menu {
        margin-top: 2.2rem;
      }
    }


    li {
      display: inline-block;
      margin-left: 2rem;
      font-weight: bold;

      @include breakpoint($mobile) {
        display: block;
        margin: 0;
        margin-bottom: 1rem;
      }

      a {
        color: #fff;
      }
    }
  }

  &__copy {
    margin-top: 5.5rem;
    width: 100%;
    text-align: center;
    color: #bdbdbd;

    a {
      color: #fff;
    }
  }

  @include breakpoint($tablet) {
    flex-direction: column;
    justify-content: center;
    padding-bottom: 2rem;

    &__menu {
      margin-top: 2.5rem;
    }

    .forHide {
      display: none;
    }

    .hide {
      display: inline-block;
    }

    > * {
      width: auto;
      text-align: center !important;
    }

    &__copy {
      margin-top: 1.5rem;
    }

    &__logo {
      float: none;
      margin-top: 4rem;
    }
  }

  a:hover {
    text-decoration: none;
  }
}
