.modal-body #coursesList {
  display: block !important;
}

#coursesList > li:not(.slide) {
  display: none;
}


.popupVideo {
  background: #000;

  &__container {
    @include transition(opacity 0.4s ease-out .8s);
    opacity: 0;
  }


  &__container {
    @extend .embededVideo;
  }

  @include breakpoint($mobile) {
    width: 100%;
    float: none;
  }

  &__thumbnail {
    @extend .absoluteCenter;
    z-index: 5;
    width: 100%;
    height: 100%;
    cursor: pointer;
  }

  .playVideoControl {
    @include transition(all 1s .5s);
    cursor: pointer;
  }

}


#azuremediaplayer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: auto !important;
  height: auto !important;

  video {
    width: 100%;
    height: 100%;
  }
}

.popupContent {
  overflow: hidden;
  @extend .flex;
  @include breakpoint($only-desktop) {
    flex-wrap: nowrap;
    align-items: stretch;
  }

  &__left {
    padding: 1.6rem 3.2rem;
    width: 70%;
    flex-shrink: 0;

    @include breakpoint($xdesktop) {
      width: 65%;
    }

    @include breakpoint($tablet) {
      width: 100%;
    }

  }

  &__navigation {
    margin-bottom: 2rem;
    color: $brand-green;
    font-size: 11px;
    font-weight: bold;

    a {
      color: $brand-green;
      text-transform: uppercase;
      text-decoration: none !important;
    }
  }

  .featuresList {
    width: auto;
    overflow: hidden;
    background-color: $gray-bg;
    padding:3.2rem 3.2rem 0 3.2rem;


    @include breakpoint($tablet) {
      font-size: 1.5rem;
      min-width: 100%;
    }
  }

  @include breakpoint($mobile) {
    width: 100%;
    float: none;
  }

  &__title{
    @extend .h2;
    font-weight: bold;
    margin-bottom: 1rem;
  }

  &__text {
    @include breakpoint($mobile) {
      font-size: 1.5rem;
    }
  }

  &__subTitle{
    margin-bottom: 1.6rem;
    font-size: $default-font-size-fixed;
    text-transform: uppercase;
    color:$top-menu-color;

    @include breakpoint($mobile) {
      font-size: 13px;
    }
  }
}

// slider section
.slick-slide:first-child .popupContent__navigation i,
.slick-slide:last-child .popupContent__navigation i,
.slick-disabled {
  display: none !important;
}

.slick-current .popupVideo__container{
  opacity: 1;
}


//azure player skin customization
.modal-body .amp-default-skin .vjs-big-play-button {
  border-radius: 100%;
  width: 10rem;
  height: 10rem;
  border: none;
  box-shadow: none !important;
  background-color: rgba(255, 255, 255, 0.5) !important;
  margin: 0 !important;
  @include transform(translate3d(-50%,-50%,0));
  font-size: 2.8rem;

  &:before {
    text-shadow: none;
    line-height: 10rem !important;
  }
}
