//columned blocks mixin
.twoCol,
.threeCol,
.fourCol {
  @extend .flex;
  justify-content: flex-start;
  align-items: flex-start;
  justify-content: flex-start;

  img {
    flex-shrink: 0;
  }

  .cItem {
    margin-bottom: 2rem;
    margin-right: 2rem;
  }

  @include breakpoint($mobile) {
    flex-direction: column;
    .cItem {
      min-width: 100%;
      margin-right: 0 !important;
      margin-left: 0 !important;
    }
  }

  //because of isotope can't recognize flex correctly
  &.filterresults {
    .cItem {
      margin-right: 0;
    }
  }
}

.twoCol {
  .cItem {
    @include calc(width, '99.99% * 1/2 - (20px - 20px * 1/3)');
    &:nth-child(2n) {
      margin-right: 0;
    }
  }
}

.threeCol {
  .cItem {
    @include calc(width, '99.99% * 1/3 - (20px - 20px * 1/3)');

    &:nth-child(3n) {
      margin-right: 0;
    }
  }

  @include breakpoint($tablet) {
    justify-content: space-between !important;

    .cItem {
      @include calc(width, '(99.99% * 1/2) - (20px * 1/2)');
      margin-right: 0;
    }
  }
}

.fourCol {
  .cItem {
    @include calc(width, '99.99% * 1/4 - (20px - 20px * 1/4)');

    &:last-child {
      margin-right: 0 !important;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }

  @include breakpoint($desktop) {
    .cItem {
      &:nth-child(4n) {
        margin-right: 20px;
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
      @include calc(width, '(99.99% * 1/3) - (20px - 20px * 1/3)');
    }
  }

  @include breakpoint($tablet) {
    .cItem {
      &:nth-child(1n),
      &:nth-child(3n) {
        margin-right: 20px;
      }
      &:nth-child(2n) {
        margin-right: 0;
      }
      @include calc(width, '(99.99% * 1/2) - (20px - 20px * 1/2)');
    }
  }
  @include breakpoint($tablet) {
    .cItem {
        margin-right: 0;
    }
  }
}


.flexThirdColumn {
  .cItem {
    width: 100%;
  }
}

.flexThirdColumn {
  @include calc(width, '99.99% * 1/3 - (20px - 20px * 1/3)');
  flex-shrink: 0;
  margin-left: 20px;

  @include breakpoint($tablet) {
    @include calc(width, '50% - (20px/2)');
  }
}



.filtersBlock {
  width: 80%;
  margin: auto;
  @extend .flex;
  margin-top: 6rem;
  margin-bottom: 6rem;
  justify-content: center;


  &__item {
    position: relative;

    @include breakpoint($mobile) {
      margin-bottom: 5rem !important;
    }
  }

  &.twoCol {
    width: 100%;
    max-width: 600px;

    @include breakpoint($tablet) {
      width: 70%;
    }

    @include breakpoint($mobile) {
      width: 90%;

      .filtersBlock__item {
        margin-bottom: 5rem;
      }
    }
  }

  .select2 {
    width: 100%;
    max-width: 100%; //it need when you resize parent select2 container
  }

}

.select2-results__option {
  @extend .flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: nowrap;

  img {
    max-width: 20px;
    max-height: 25px;
    width: auto;
    height: auto;
    margin-right: 20px;

  }

  span:first-child {
    width: 35px;
  }

  .icon {
    @extend .flex;
    align-items: center;
  }
}


.iconBlock {
  @extend .flex;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: flex-start;
  margin-bottom: 1.5rem;

  img {
    max-width: 20%;
    margin-right: 2rem;
  }

  p {
    margin: 0;
  }
}
