.iMap {
	padding: 6rem;
  padding: 0;
	display: flex;
	justify-content: center;
	text-align: center;
	padding-bottom: 5rem !important;
	overflow: hidden;
	position: relative;

	@include breakpoint(max-width 1200px) {
		padding-bottom: 8rem !important;
	}

  * {
    margin: 0 !important;
    padding: 0 !important;
  }

	// &__texts {
	// 	opacity: 0;
	// 	@include transition(all 0.15s ease-out 0s);
	// }
	//
	// &.on {
	// 	.iMap__texts {
	// 		opacity: 1;
	// 	}
	// }

	&__wrapper {
		position: relative;
    float: left;
		min-height: 300px;
	}

	&__header {
		position: absolute;
		left: 0;
		right: 0;
		top: 4rem;
		font-size: 2.8rem;
		z-index: 5;
		padding: 0 5rem !important;

		@include breakpoint(max-width 960px) {
			top: 0;
		}
	}

	&__info {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		font-size: 1.8rem;
		z-index: 5;

		@include breakpoint(max-width 1200px) {
			bottom: 4rem;
		}

		@include breakpoint(max-width 640px) {
			bottom: -3rem;
		}


		ul {
			display: flex;
			justify-content: space-between;
			padding: 0 5rem !important;
			display: flex;
			line-height: normal;

			li {
				text-align: center;
				width: 33%;
				list-style-type: none;

				p {
					font-size: 1.8rem;
				}

				i {
					font-style: normal;
				}
			}

			strong {
				color: #E52591; //$pink
				font-size: 2.8rem;
				display: block;
				margin-bottom: 1rem !important;
			}

			@include breakpoint(max-width 640px) {
				flex-direction: column;
				justify-content: center;

				p {
					font-size: 2rem;
				}

				li {
					width: 100%;
					text-align: center;
					padding-bottom: 5rem !important;
				}
			}
		}

		&.on {
			.iMap__info,
			.iMap__header {
				opacity: 1;
			}
		}

	}

	img {
		max-width: 100%;
		height: auto;
    display: block;

		@include breakpoint(max-width 640px) {
			transform: scale(1.5);
			transform-origin: top center;
		}
	}

	&__dots {
		opacity: 0.85;

		@include breakpoint(max-width 640px) {
			display: none;
		}

		li {
			position: absolute;
			display: block;
			border-radius: 100%;
			background: #088896;
			z-index: 5;
			transform: translate3d(-50%, -50%, 0);
			margin: 0;
			padding: 0;
			opacity: 0;
			left:50%;
			top:50%;

			@include breakpoint(max-width 1200px) {
				transform: translate3d(-50%, -50%, 0) scale(0.75);
			}

			&:hover {
				opacity: 1;
			}

			&.xs {
				width: 4px;
				height: 4px;
				@include transition(all 1s ease-in-out 100ms);
			}

			&.s {
				width: 8px;
				height: 8px;
				@include transition(all 800ms ease-in-out 400ms);
			}

			&.m {
				width: 12px;
				height: 12px;
				@include transition(all 600ms ease-in-out 700ms);
			}

			&.l {
				width: 14px;
				height: 14px;
				@include transition(all 800ms ease-in-out 900ms);
			}

			&.xl {
				width: 20px;
				height: 20px;
				@include transition(all 1200ms ease-in-out 900ms);
			}

			i {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				top: 0;
				right: 0;
				bottom: 0;
				background: #088896;
				border-radius: 100%;
				opacity: 0.3;
			}

			i {
				@include animation(scale 1.0s linear);

				@include breakpoint(max-width 640px) {
					@include animation(none !important) ;
				}
			}

			i + i {
				@include animation(scale2 1.0s linear);
			}

		}

	}


  @keyframes scale {
    0% {transform: scale(0);}
    50% {transform: scale(1.8); opacity: .3}
    100% {transform: scale(2.5); opacity: 0}
  }

  @keyframes scale2 {
    0% {transform: scale(0);}
    50% {transform: scale(3); opacity: .3}
    100% {transform: scale(6); opacity: 0}
  }

  @-webkit-keyframes scale {
    0% {transform: scale(0);}
    50% {transform: scale(1.8); opacity: .3}
    100% {transform: scale(2.5); opacity: 0}
  }

  @-webkit-keyframes scale2 {
    0% {transform: scale(0);}
    50% {transform: scale(3); opacity: .3}
    100% {transform: scale(6); opacity: 0}
  }


  &.on li {
    opacity: 1;
  }
}
