#{$all-text-inputs},
select {
  border: none;
  background: none;
  border-bottom: 1px solid $border-color;
  padding: .8rem .3rem;
  @include calc(width, '100% - .6rem');
  font-size: $default-font-size;
  font-family: $default-font;
  @include appearance(none);
  box-sizing: content-box;
  border-radius: 0;

    &:focus {
      outline: 0;
    }

    &[value=""] {
      background: #000;
    }
}

select {
  padding-left: 0;
  background: url(data:image/png;base64,) right center no-repeat;

  option {
  }

  &:focus {
    background: none;
    background: url(data:image/png;base64,)  right center no-repeat;
  }
}

textarea {
  resize: vertical;
}

.form {
  @extend .flex;
  @extend .transparentWhite;
  justify-content: space-between !important;
  padding: 9rem 9.5rem;
  width: 125rem;
  overflow: hidden;
  max-width: 95vw;

  &__left{
    width: 40%;

    .btn {
      margin-bottom: 6rem;
    }
  }

  &__right{
    width: 50%;
  }

  &__header {
    margin-bottom: 3.5rem;
  }

  &__subHeader {
    margin-top: -2rem;
    margin-bottom: 3.5rem;
    font-size: $bigger-font-size;
  }

  @include breakpoint($xdesktop) {
    width: 105.5rem;
  }

  @include breakpoint($desktop) {
    padding:10rem 5rem;
  }

  @include breakpoint($tablet) {
    width: auto;
    flex-direction: column !important;

    > * {
      width: 100%;
    }
  }

  @include breakpoint($mobile) {
    padding: 3rem 3rem;
  }
}

.mailsList {
  &__header{
    color: #9d9d9d;
    font-size: 1.4rem;
    margin-bottom: 1.2rem;
  }

  &__text{
    margin-bottom: 4rem;

  }
}

.formRow {
  position: relative;
  margin-bottom: 2.5rem;
  width: 100%;
  @extend .flex;
  flex-wrap: nowrap;
  align-items: flex-end;

  &__icon {
    display: block;
    width: 45px;
    height: 35px;
    background: url(../img/icon_username.svg);
    background-position: 0 center;
    background-size: 25px 25px;
    background-repeat: no-repeat;

    &.password {
      background-image: url(../img/icon_password.svg);
    }
  }

  .formRow__icon + input + label {
    left: 40px;
  }

  &--shorter {
    width: 70%;

    @include breakpoint($tablet) {
      width: 100%;
    }
  }

  @include breakpoint($tablet) {
    width: 48% !important;
  }

  @include breakpoint($mobile) {
    width: 100% !important;
  }

  &__label {
    position: absolute;
    left: 2px;
    // top: 5px;
    top: 5px;
    color: $inactive-label;
    @include transform-origin(left center);
    @include transition(transform 0.1s ease-out 0s);
    cursor: text;
    -webkit-font-smoothing: antialiased;
    white-space: nowrap;
    font-size: $smaller-font-size-fixed;

    &.hided {
      opacity: 0;
    }
  }

  &.phone {
    width: 50%;

    .formRow__label {
      left: 5rem;
    }
    .formRow__input {
      @include calc(width, '100% - 58px');
    }

    .intl-tel-input {
      display: block;
    }
  }

  &.error {
    .formRow__label {
      color: red;
    }
  }

  &__input {
    @include transition(margin 0.25s ease-out 0s);

    &:focus {
      margin-top: 10px;
      outline: 0;
      border-color: $red;
      border-width: 2px;
      padding-bottom: .7rem;
    }

    &:focus + label{
      @include transform(translate3d(0,-15px,0) scale(0.8));
      color: $red;
    }
  }

  &.notEmpty {
    input {
      margin-top: 10px;
    }

    label{
      @include transform(translate3d(0,-15px,0) scale(0.8));
    }
  }

  select + label {
    @include transform(translate3d(0,-15px,0) scale(0.8));
  }

}

.selectChanged label{
  @include transform(translate3d(0,-20px,0) scale(0.8));
  color: $pink;
  z-index: 10;
  opacity: 1 !important;
}


.loginDialog {
  padding: 3.5rem;
  padding-bottom: 0;
  background: #fff;
  text-align: center;
  text-transform: none;
  position: absolute;
  right: 0;
  top: 50px;
  min-width: 31rem;
  display: none;
  z-index: 1000;
  box-shadow: 0px 2px 10px 0px rgba(0, 0, 0, 0.2);

  @include breakpoint($mobile) {
    width: 40rem;
  }

  .showLogin & {
    display: block;
  }

  &:before {
    content: '';
    display: block;
    position: absolute;
    right: 13px;
    top: -9px;
    @include triangle(18px, #fff, up);
  }

  &__wrapper {
    display: inline-block;
    width: 100%;
  }

  &__title {
    line-height: 4rem;
    font-weight: bold;
    font-size: $smaller-font-size-fixed;
    margin-bottom: 5px;
  }

  &__bottomLine {
    clear: both;
    margin: 0 -3.5rem;
    margin-top: 5.5rem;
    text-align: center;
    line-height: 6.5rem;
    // font-size: $smallest-font-size-fixed;
    background: $gray-bg;
    border-top: 1px solid $gray-border;
    font-size: 12px;
    color: #000;

    a {
      color: $brand-green !important;
    }
  }
}

.globalOverlay {
  @extend .popupOverlay;
  background: none;

  .showLogin & {
    visibility: visible;
  }
}
