.coursesTop {
  @extend .flex;
  flex-flow: nowrap;
  align-items: stretch;
  min-height: 35rem;

  @include breakpoint($mobile) {
    flex-flow: wrap;
    flex-direction: column;

    > * {
      width: 100% !important;
    }
  }

  &__video {
    @extend .bgCover;
    width: 50%;

    @include breakpoint($mobile) {
      min-height: 200px;
    }
  }

  .h2 {
    font-weight: 600;
    color:#fff;
    margin-bottom: 2rem;
  }

  &__text {
    width: 50%;
    padding: 10rem 3rem 3rem;
    @extend .darkBg;


    .feat {
      font-size: 1.4rem;
    }

    p {
      @include breakpoint($only-desktop) {
        max-width: 45rem;
      }
    }

    @include breakpoint($mobile) {
      padding-top: 4rem;
      p {
        font-size: $default-font-size;
      }
    }
  }
}

.coursesFilters {
  @extend .threeCol;
  @include calc(max-width, '80% + 2rem');
  max-width: 80%;
  margin: auto;
  margin-top: 8rem;

  @include breakpoint($mobile) {
    max-width: 90%;
    width: 90%;
  }
}

.coursesList {
  @extend .flex;
  @extend .outline-margin;
  justify-content: flex-start;
  flex-shrink: 0;
}

.coursesItem {
  @include calc(width, '25% - 2rem');
  border: 1px solid $border-color;

  &__video {
    position: relative;
    display: block;

    .icon-play-circle {
      @extend .absoluteCenter;
      color: #fff;
      font-size: 6.5rem;
    }
  }

  &__description {
    @extend .outline-margin;
  }

  &__img {
    width: 100%;
    height: auto;
  }

  &__title {
    font-size: $bigger-font-size;
    color: $brand-green;
    font-weight: 600;
    margin-bottom: 2rem;
  }

  .viewMore {
    color: $brand-green;
    font-size: 1.4rem;
  }
}

.filterresults {
  @include transition(all 0.5s ease-out 0s);
}
