/* Breakpoints  ----------------------------------------------------- */
$mobile:		    max-width 640px;
$tablet:		    max-width 960px;
$desktop:		    max-width 1200px;
$only-desktop:            960px;
$not-mobile:              640px;
$xdesktop:	    max-width 1440px;
$small-height:  max-height 800px;
$xsmall-height: max-height 800px;
$lanscape:      orientation landscape;


/* ==========================================================================
Colors
========================================================================== */
$main-color: #212121;
$gray-text: #bdbdbd;
$dark-text: #3D3D3D;
$text-gray-color:#8a8a8a;
$border-color:#e1e1e1;
$gray-bg:#f2f2f2;
$brand-green: #088896;
$block-bg: #fbfbfb;
$top-menu-color:#6f6f6f;

$red: #f44336;
$pink: #E52591;
$pink-hover: #e83c9c;
$green: #088896;
$blue:#535e7e;
$blue-light:#2279b6;
