/* ==========================================================================
Typography
========================================================================== */
$default-font: proxima-nova-alt, Helvetica, Arial, Verdana, sans-serif;
$main-font: HelveticaNeueLt;
$thin-font: HelveticaNeueThin;
$medium-font: helveticaNeueMedium;

$default-font-size: 1.6rem;
$default-font-size-fixed: 16px;
$smaller-font-size: 1.4rem;
$smaller-font-size-fixed: 14px;
$smallest-font-size: 1.2rem;
$smallest-font-size-fixed: 12px;
