  -moz-transition: -moz-transform 0.35s cubic-bezier(0.54, 0.39, 0.4, 1.02) 0s, opacity 0.4s cubic-bezier(0.54, 0.39, 0.4, 1.02);
  transition: transform 0.35s cubic-bezier(0.54, 0.39, 0.4, 1.02) 0s, opacity 0.4s cubic-bezier(0.54, 0.39, 0.4, 1.02);
}

.modal-container.normalShow {
  -webkit-transition: all 0.35s ease-out 0s;
  -moz-transition: all 0.35s ease-out 0s;
  transition: all 0.35s ease-out 0s;
  visibility: visible;
  opacity: 1;
}

