.initialTopPadding {
  margin-top: 80px;
}

.backgroundGraphic {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;

  &.imgBg {
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  video {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    left: 50%;
    transform: translateX(-50%) translateY(0%);
    background-size: cover;
    background-position: center center;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;;
  }

  &:after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.4);
  }
}

.introContent {
  color: #fff;
  height: 100%;
  text-align: center;
  position: relative;
  z-index: 5;
  display: flex;
  flex-direction: column;
  justify-content: center;

  &__btnsContainer {
    display: table;
    margin: auto;
  }

  @include breakpoint($mobile) {
    padding: 2rem;
  }

  @include breakpoint($tablet $lanscape) {
    margin-top: 3rem;
  }

  * {
    font-family: $default-font;
  }

  h1 {
    font-size: 6.5rem;
    margin-bottom: 2rem;
    font-weight: normal;
    max-width: 90vw;
    margin-left: auto;
    margin-right: auto;

    @include breakpoint($mobile) {
      font-size: 4.5rem;
    }

    @include breakpoint($tablet $lanscape) {
      font-size: 4.5rem;
    }
  }

  h2 {
    font-size: 3.6rem;
    font-weight: 300;
    margin-bottom: 3rem;
    max-width: 90vw;
    margin-left: auto;
    margin-right: auto;

    @include breakpoint($mobile) {
      font-size: 3.5rem;
    }
    @include breakpoint($tablet $lanscape) {
      font-size: 3.5rem;
    }
  }

  .btn {
    @extend .big--btn;
    width: 100%;
    min-width: 200px;
  }
}
