.positionsDescription {
  // @extend .twoCol;
  overflow: hidden;
  padding-left: 2rem;
  font-size: 2rem;
  width: 50%;
  line-height: 2.8rem;
  margin-bottom: 4rem;

  .cItem {
    // display: none;
    max-height: 0;
    float: left;
    overflow: hidden;

    p {
      margin: 0;
    }

    &.show {
      @include transition(all 0.5s ease-out 0s);
      display: block;
      max-height: 300px;
    }

    .iconBlock__subtitle {
      font-size: 1.6rem;
      color: $text-gray-color;
    }
  }

  img {
    max-width: 52px;
    max-height: 45px;
    height: auto;
    width: auto;
    margin-right: 3rem;

    & + div {
      line-height: normal;
    }
  }
}

.positionsBlock {
  @extend .threeCol;
  justify-content: center;

  &__item {
    padding: 2rem;
    text-decoration: none !important;
    border: 1px solid $border-color;
    // color: $text-gray-color !important;
    color: #000 !important;
    position: relative;

    img {
      max-width: 40px;
      max-height: 40px;
      height: auto;
      width: auto;
      position: absolute;
      right: 2rem;
      bottom: 2rem;
      @include transition(all 0.25s ease-out );
      @include filter(grayscale(100%));
      opacity: 0.5;
    }

    @include breakpoint($only-desktop) {
      &:hover {
        box-shadow: 0 0 3px 0 rgba(0, 0, 0, 0.1);

        img {
          @include filter(none);
          opacity: 1;
        }
      }
    }


    p {
      margin: 0;
    }

    h4 {
      font-weight: bold;
      color: $brand-green;
      margin-bottom: 2rem;
    }
  }
}

.aboutSection {
  margin-bottom: 8rem;

  &__item {
    @extend .flex;
    justify-content: space-between;
    flex-wrap: nowrap;
    margin-bottom: 2rem;

    @include breakpoint($mobile) {
      flex-wrap: wrap;
      flex-flow: column;
    }
  }

  &__copy {
    @extend .p10x8;
    border: 1px solid $border-color;
    @include font-size($bigger-font-size);

    @include breakpoint($mobile) {
      margin-right: 0;
    }

    em {
      color: $pink;
    }
  }

  &__title {
    color: $text-gray-color;
    margin-bottom: 2rem;
  }

  &__img {
    position: relative;
    @extend .flexThirdColumn;
    @extend .mobileWide;
    background-position: center center;
    background-size: cover;

    @include breakpoint($mobile) {
      order: -1;
      min-width: 100%;
      min-height: 30rem;
      margin-left: 0;
    }

    p {
      color: #fff;
      background-color: rgba(0, 0, 0, 0.2);
      @extend .absoluteFill;
      top: auto;
      padding: 1rem;
    }

    &:hover {
      .aboutSection__hoverImg {
        display: block;
      }
    }

  }
  &__hoverImg {
    display: none;
    @extend .absoluteFill;
  }
}

.perksSection {
  @extend .threeCol;
  margin-top: 6rem;

  &__item {
    @extend .flex;
    flex-wrap: nowrap;
    border: 1px solid $border-color;
    align-items: center;
    justify-content: flex-start;
    padding:0 3rem;
    background-color: $block-bg;
    margin-bottom: 2rem;
    height: 11rem;

    @include breakpoint($mobile) {
      height: auto;
      padding: 3rem;
      min-height: 12rem;
    }

    p {
      margin: 0;
      @include font-size($bigger-font-size);
    }

    img {
      max-width: 4.5rem;
      max-height: 4.5rem;
      width: auto;
      height: auto;
      margin-right: 2rem;
      flex-shrink: 0;
    }
  }

}

.questionsSection {
  background-color: $gray-bg;
  padding-top: 4rem;
  text-align: center;

  img {
    max-width: 100%;
    height: auto;
  }

  .iconBlock {
    text-align: left;
    padding-left: 4rem;
    padding-bottom: 4rem;

    img {
     border-radius: 100%;
     width: 70px;
     height: auto;
   }
  }
}

//detail page
.careerDescription {
  @extend .flex;
  @extend .mobileWide;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 5rem;

  .h3 {
    margin-bottom: 6rem;
  }

  > div {
    border: 1px solid $border-color;
    @extend  .outerPadding5;

    //width auto for column
    &:first-child {
      flex-grow: 1;
    }
  }

}
