html {
	font-size: 62.5%;
}

body {
  font-family: $default-font;
	@include font-size($default-font-size);
  line-height: 1.43;
}

/*===============================
=            Headers            =
===============================*/
h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  line-height: 1.1;
	text-rendering: optimizeLegibility;
}

h1,.h1 {
  font-size: 36px;
	font-weight: bold;
}

h2,.h2 {
  font-size: 2.8rem;
}

h3,.h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 18px;
}

.h5, h5 {
	font-size: $default-font-size;
  font-weight: 600;
	margin-top: 1.5rem;
	margin-bottom: 1.5rem;
}

.editorContent {
  h3 {
    @extend .h5;
  }
	ul {
		margin: 1.5rem 2rem;
		list-style-type: disc;
		list-style-position: inside;
	}
}


/*=====  End of Headers  ======*/
strong,
.bold {
  font-weight: bold;
}

p {
  margin-bottom: 1.5rem;
	word-wrap: break-word;
}


.special {
  font-style: italic;
}

.firm {
  font-variant: small-caps;
}

button, html, input, select, textarea {
    font-family: $main-font;
}
