@font-face {
  font-family: 'icomoon';
  src:url('../fonts/icomoon.eot?jqm9if');
  src:url('../fonts/icomoon.eot?jqm9if#iefix') format('embedded-opentype'),
      url('../fonts/icomoon.ttf?jqm9if') format('truetype'),
      url('../fonts/icomoon.woff?jqm9if') format('woff'),
      url('../fonts/icomoon.svg?jqm9if#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
@import url(https://fonts.googleapis.com/css?family=Roboto:400,700);

[class^="icon-"], [class*=" icon-"],.icon-font {
	display: inline-block;
	vertical-align: middle;
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  text-transform: none;
  // line-height: 1; macosfix
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
	text-align: center;
	font-size: 2.8rem;
	white-space: nowrap;
	word-spacing: 0;
	margin-right: 1rem;
}

