.featuresList{
  width: 90%;

  span {
    overflow: hidden;
    display: block;
  }

  &__item {
    margin-bottom: 1.5rem;
    overflow: hidden;

    @include breakpoint($mobile) {
      margin-bottom: 3rem;
    }

    &:before {
      content: '';
      display: block;
      background: url(../img/lesson-icon.svg) 0 0 no-repeat;
      height: 4.5rem;
      width: 5.5rem;
      float: left;
    }

    &.book-icon:before {
      background: url(../img/assessments-icon.svg) 0 0 no-repeat;
    }

    &.languages-icon:before {
      background: url(../img/languages-icon.svg) 0 0 no-repeat;
    }

  }

  &__text {
    overflow: hidden;
  }

  &__header {
    display: block;
    text-transform: uppercase;
    font-size: 11px;
    color: $gray-text;
    margin-bottom: 5px;
  }

}


.socialSection {
  text-align: center;
  padding: 8rem 0 6rem 0;

  h3 {
    line-height: 4rem;
    margin-bottom: 2rem;
    font-weight: normal;
  }
}

.socialMenu {
  text-align: center;
  z-index: 2;
  display: inline-block;

  @include breakpoint ($mobile) {
    padding: 0 1rem;
  }

  &__item {
    display: inline-block;
    margin: 0 0.5rem;
    border: 1px solid $pink;
    width: 5.5rem;
    height: 5.5rem;
    line-height: 5.5rem;
    text-align: center;
    font-size: 0.8em;
    position: relative;
    font-size: 0;
    text-align: center;
    box-sizing: content-box;
    overflow: hidden;

    @include breakpoint ($mobile) {
      width: 4.2rem;
      height: 4.2rem;
      margin: 0 0.1rem;
      line-height: 4.5rem;
    }

    [class^="icon-"], [class*=" icon-"] {
      margin: 0;
      font-size: 2.1rem;
      color: $pink;
    }

    a {
      display: block;

      @include breakpoint ($only-desktop) {
        &:hover {
          background: $pink;

          i {
            color: #fff;
          }
        }
      }
    }
  }
}
