.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.hide {
  display: none;
}

.off {
  opacity: 0;
}

// popup opened
.oh {
  overflow: hidden !important;
}

@include breakpoint($only-desktop) {
  //shift for body and fixed elements when popup opened
  body.oh:not(.mac-os):not(.ios) {
    margin-right: 17px;

    .shiftMargin {
      margin-right: 17px !important;
    }
  }
}


// popup opened END

.over {
  overflow: hidden;
}

.text-light {
  color: $text-gray-color;
}

.flR {
  float: right;
}

.text-lighter {
  color: $gray-text;
}

.clrfx {
  display: block;
  clear: both;
  height: 0;
  width: 100%;
}

.text-line {
  text-align: center !important;
}

.text-line:after {
  content: "";
  clear: both;
  display: block;
  margin: 2rem auto 0;
  width: 10rem;
  height: 1px;
  border-top: 2px solid $pink;
}

.l-container {
  margin: 0 2rem;
}

.brand-color {
  color: $pink;
}

[v-cloak] {
  display: none;
}

.transparent {
  opacity: 0;
  z-index: -1 !important;
}
