.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .5);
  @extend .flex;
  justify-content: center;
  align-items: center;
  transition: opacity .35s ease;

  &.dark {
    background-color: rgba(0, 0, 0, .8);
  }
}

.fullscreenVideo {
  height: 90vh;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; // 16/9
}

.modal-wrapper {

  @include breakpoint($mobile) {
    max-width: 95vw;
    margin: auto;
  }
}

.modal-container {
  width: 95rem;
  max-width: 85vw;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  @include transform(scale(.5));
  visibility: hidden;
  position: relative;
  opacity: 0;
  overflow: hidden;

  @include breakpoint($small-height) {
    width: 75rem;
  }

  &.fullscreen-popup {
    opacity: 1;
    visibility: visible;
    @include transform(scale(1));
    @include transition(all 0.35s ease-out 0s);
    background: none;
    background: #000;
    width: 85vw;
    max-width: 120rem;

    @include breakpoint($mobile) {
      width: 95vw;
    }

    .modal-body {
      overflow: hidden;

      @include breakpoint($tablet) {
        max-height: 85vh;
        -webkit-overflow-scrolling: touch;
        overflow: auto;
      }
    }

    .modalCloseButton {
      position: fixed;
    }
  }

  //class for courses popup shifting
  &.transitionOn {
    opacity: 1;
    visibility: visible;
    @include transition(transform .35s cubic-bezier(.54,.39,.4,1.02) 0s, opacity .4s cubic-bezier(.54,.39,.4,1.02));
  }

  //popup showing without shift
  &.normalShow {
    @include transition(all .35s ease-out 0s);
    visibility: visible;
    opacity: 1;
  }
}

.modal-body {
  max-height: 80vh;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-default-button {
  float: right;
}

.modalCloseButton {
  position: absolute;
  right: 1rem;
  top: 1.5rem;
  padding: 0;
  border: none;
  cursor: pointer;
  width: 4rem;
  height: 4rem;
  color: $gray-text;
  font-size: 3.2rem;
  outline: none;
  background: #000;
  border-radius: 100%;

  &:before {
    position: relative;
    top: -1px;
  }

  @include breakpoint($tablet) {
    width: 40px;
    height: 40px;
    position: fixed;
    background: none;
    z-index: 100;
    border-radius: 100%;
    right: 0;
    top: 5px;
  }
}
/*
 * the following styles are auto-applied to elements with
 * v-transition="modal" when their visiblity is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter, .modal-leave {
  opacity: 0;
}

.modal-enter .modal-container.fullscreen-popup,
.modal-leave .modal-container.fullscreen-popup {
  @include transform(scale(0.2));
}


// ************************ios section************************
.ios {
  //preventing native controls for iso
  .modal-wrapper{
    *::-webkit-media-controls-panel {
      display: none !important;
      -webkit-appearance: none;
    }

    *::--webkit-media-controls-play-button {
      display: none !important;
      -webkit-appearance: none;
    }

    *::-webkit-media-controls-start-playback-button {
      display: none !important;
      -webkit-appearance: none;
    }
  }
}
