.btn {
	display: inline-block;
	text-decoration: none !important;
	border: none;
	line-height: 40px;
	height: 42px;
	padding:0 1.2rem;
	text-transform: uppercase;
	color: $green;
	// box-sizing: content-box;
	border: 1px solid $pink;
	border-radius: 3px;
	transition: box-shadow .4s cubic-bezier(.25,.8,.25,1), background-color .4s cubic-bezier(.25,.8,.25,1);
	font-size: 14px;
	font-family: $default-font;
	background: $pink;
	color: #fff;
	cursor: pointer;
	@include transition(all 0.15s ease-out 0s);
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.29);
	text-align: center;

	> * {
		display: inline-block;
		vertical-align: middle;
	}

	&.sm {
		line-height: 3.8rem;
		height: 3.8rem;

		@include breakpoint($mobile) {
			line-height: 38px;
			height: 38px;
		}
	}

	&.static-sm {
		line-height: 38px;
		height: 38px;
		box-shadow: none;
	}


	@include breakpoint($mobile) {
		padding:0 10px;
  }

	&:hover {
		background-color: $pink-hover;
		border-color: $pink-hover;
		color: #fff;
	}

	&:active {
		@include transform(scale(0.95));
	}

	&.bordered {
		@include bordered-btn($brand-green);
	}
	// button with transparent white bg
	&.white-border {
		border-color: #fff;
		color: #fff;
		background-color: rgba(255, 255, 255, 0.3);

		&:hover {
			background-color: rgba(255, 255, 255, 0.5);
		}
	}
}

